import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/views/home/index.vue'
import Add from '@/views/add/index.vue'

Vue.use(Router)

export default new Router({
  mode: 'history',
  base: process.env.BASE_URL,
  routes: [
    {
      path: '/',
      name: 'home',
      component: Home
    }, {
      path: '/add',
      name: 'add',
      component: Add
    }, {
      path: '/add/:id',
      name: 'add',
      component: Add
    }
  ]
})
